<template>
  <div class="homePage">
    <!-- 搜索框 -->
    <div>
      <van-search v-model="value" show-action background="#ddd" placeholder="请输入搜索关键词">
        <template #action>
          <div @click="onSearch">搜索</div>
        </template>
      </van-search>
    </div>

    <div class="searchData" :style="{display: showOff}">
      <div class="off" @click="Off">×</div>
      <div class="searchData_item">
        <div class="clearfix">
          <div class="searchData_imgData fl" @click="goDetail('mySearchData',item.name,mySearchData.length-1)" v-for="(item,index) in showSearchData" :key="index">
          
              <div class="searchData_img">
                <img v-lazy="item.coverURL" />
              </div>
              <div>
                <div class="searchData_imgData_title">{{item.name}}</div>
                <div class="on-title popularMenu_imgData_sec">{{item.desc}}</div>
              </div>
          </div>
          
        </div>
      </div>
    </div>
    <div class="homepage_content">
      <div class="max_menu_list" ref="maxMenuList">
      <van-list
        v-model="lozyLoading.loading"
        :finished="lozyLoading.finished"
        finished-text="没有更多了"
        @load="onLoad"
        :offset="50"
        class="fontSizeColor"
      >
        <!-- =================================================== -->
           
             <!-- 选项 -->
              <div class="menu_list">
                <!-- <div> -->
                  <ul class="clearfix">
                    <li class="fl menu_list_pets" v-for="(item,index) in petsList" :key="index" @click="petsListClick(index)">
                        <div class="menu_list_pets_img">
                        
                          <img
                            :src="item.imgSrc"
                            :class="{pets_img_icon: petsStyleData[index].isActive?'pets_img_icon':''}"
                          />
                        </div>
                        <div
                          class="menu_list_pets_name"
                          :class="{isactive: petsStyleData[index].isActive?'isactive':''}"
                        >{{item.title}}</div>
                    </li>
                  </ul>
                <!-- </div> -->
              </div>

              <!-- 宠物展示 -->
              <div class="popularMenu">
                <!-- <div class="popularMenu_title">猫科</div> -->
                <div class="popularMenu_title">
                  <div class="popularMenu_title_item">{{ShowStyleName}}</div>
                </div>

                <div class="popularMenu_liatItem">
                  <div class="clearfix">
                    <div
                      class="popularMenu_imgData fl"
                      v-for="(item,index) in ShowpetsListData"
                      :key="index"
                      @click="goDetail('petsStyleData',item.name,type)"
                    >
                      <div class="popularMenu_img">
                        <img v-lazy="item.coverURL" />
                      </div>
                      <div>
                        <div class="popularMenu_imgData_title">{{item.name}}</div>
                        <div class="on-title popularMenu_imgData_sec">{{item.desc}}</div>
                      </div>
                    </div>
                  </div>
                </div>
              
           </div>
      </van-list>
      </div>
    </div>
  </div>
</template>

<script>
import '../../assets/css/HomePage.less'
import { createNamespacedHelpers } from "vuex";

const { mapState, mapMutations } = createNamespacedHelpers("homeModules");

export default {
  data() {
    return {
      lozyLoading: {
        loading: false,
        finished: false
      },
      value: "",
      images: [
        "https://img.yzcdn.cn/vant/apple-1.jpg",
        "https://img.yzcdn.cn/vant/apple-2.jpg"
      ],
      // 宠物类列表
      petsList: [
        {
          title: "猫科",
          imgSrc: require("../../assets/img/mao.png"),
          name: 0,
          isActive: true
        },
        {
          title: "犬类",
          imgSrc: require("../../assets/img/quan.png"),
          name: 1,
          isActive: false
        },
        {
          title: "爬行类",
          imgSrc: require("../../assets/img/she.png"),
          name: 2,
          isActive: false
        },
        {
          title: "小宠物类",
          imgSrc: require("../../assets/img/niao.png"),
          name: 3,
          isActive: false
        },
        {
          title: "水族类",
          imgSrc: require("../../assets/img/yu.png"),
          name: 4,
          isActive: false
        }
      ],
      ShowpetsListData: [{}],
      ShowStyleName: "",
      //显示搜索数据
      showSearchData: [],
      showOff: "",
      flagScorr: 0,
      Eementpp: null,
      linkNub: null
    };
  },
  computed: {
    ...mapState(["petsStyleData", "type","showOffData","mySearchData","searchDataShow","maskLink"])
  },
  created() {
    //  this.flagScorr=this.maskLink==null?0:this.maskLink;
    if (this.petsStyleData[this.type].petsMaxData.length >= 3) {
      this.petsData(this.type);
    };
    this.showOff=this.showOffData;
    if(this.showOffData=="block"){
      this.showSearchData=this.searchDataShow;
    }
    if(this.mySearchData.length<1){
      this.showSearchData=[];
    }
    
    this.linkNub=this.maskLink;
 
  },
  mounted(){
    // this.Eementpp=this.$refs.maxMenuList.scrollTop;
    // 
  },
  methods: {
    onLoad() {
      // if(this.petsStyleData[this.type].petsMaxData.length>=3){
        // if(this.petsStyleData[this.type].petsMaxData.length!=5){
          
            this.petsData(this.type);
        // }
    
      // }

      
    },

    ...mapMutations([
      "addAanShowPetsData",
      "changShowType",
      "changStylePage",
      "isActiveDAta",
      "addMySearchData",
      "changeShowOffData",
      "addSearchDataShow",
      "changeMaskLink"
    ]),
    petsData(type) {
      this.flagScorr=this.linkNub==null?0:this.linkNub;
      // this.Eementpp=this.$refs.maxMenuList.scrollTop;
      
      this.changShowType(type);
      let add = this.petsStyleData[type].addPage;
      add++;
      this.ShowStyleName = this.petsStyleData[type].title;
        let localSData=localStorage.getItem('pestData');
        localSData=JSON.parse(localSData);

      if (localSData['petsStyleData'][type].petsMaxData.length >= 15) {
         //获取本地存储数据         
        if(localSData!='null'){
            if(localSData['petsStyleData'][type].petsMaxData.length!=0){
              this.ShowpetsListData=localSData['petsStyleData'][type].petsMaxData;
              this.lozyLoading.finished = true;
              this.lozyLoading.loading = true;
      
              this.$nextTick(() =>{
                this.$refs.maxMenuList.scrollTop=this.flagScorr;
              });
              return;
            }
        }
        this.ShowpetsListData = this.petsStyleData[type].petsMaxData;
        
        this.lozyLoading.finished = true;
        this.lozyLoading.loading = true;
        // this.$refs.maxMenuList.scrollTop=this.flagScorr;
          this.$nextTick(() =>{
                this.$refs.maxMenuList.scrollTop=this.flagScorr;
              });
        return;
      } else {
        this.lozyLoading.finished = false;
        this.lozyLoading.loading = false;
      }
       
      //开启加载提示
      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        //延迟自动关闭加载提示，如果该值为0，则不会自动关闭
        duration: 0
      });
      this.axios({
        method: "GET",
        params: {
          key: this.appkey,
          type,
          page: add,
          num: 5
        }
      })
        .then(result => {
          this.$toast.clear();
          
          let petsDataSum = result.data.newslist;

          if (result.data.msg == "success") {
            petsDataSum.forEach(v => {
              v.Collection = false;
              v.styleTitle='petsStyleData';
              v.listType=null;
              v.saveAddNum=null;
            });
            
            this.addAanShowPetsData(petsDataSum);
            this.changStylePage({ num: type, page: add }); //type,add
            

            if (petsDataSum[0].pettype == this.ShowpetsListData[0].pettype) {
              this.ShowpetsListData.push(...petsDataSum);
              // this.ShowpetsListData = petsDataSum;
              localSData['petsStyleData'][type].petsMaxData.push(...petsDataSum);
              // localStorage.setItem('pestData',JSON.stringify(localSData));
              
            } else {
              this.ShowpetsListData = petsDataSum;
               localSData['petsStyleData'][type].petsMaxData=petsDataSum;
             
            }
             localStorage.setItem('pestData',JSON.stringify(localSData));
            //  this.$refs.maxMenuList.scrollTop=this.flagScorr;
              this.$nextTick(() =>{
                this.$refs.maxMenuList.scrollTop=this.flagScorr;
              });
          }
        })
        .catch(err => {
          this.$toast.clear();
          
        });
        
    },
    petsListClick(name) {
       this.linkNub=0;
      this.petsData(name);
      
      this.isActs(name);
      let numberw=0;
      this.changeMaskLink(numberw);
      
    },
    isActs(num) {
      if (this.petsStyleData[num].isActive) {
        
        return;
      }
      for (let i = 0; i < this.petsStyleData.length; i++) {
        if (this.petsStyleData[i].isActive) {
          this.isActiveDAta({ num: i, flag: false });
          break;
        }
      }
      this.isActiveDAta({ num: num, flag: true });
      
    },
    searchPetsName(PetsName, index) {},
    goDetail(styleName,name, type) {
      //pid: 商品id
      this.$router.push({ name: "Detail", query: { name, type,styleName } });

      let maxMenuList=this.$refs.maxMenuList.scrollTop;
      // 
      this.changeMaskLink(maxMenuList);

    },
    onSearch() {
      if (this.value == "") {
        this.$toast("输入不能为空");
        return;
      }

      this.$toast.loading({
        message: "加载中...",
        forbidClick: true,
        duration: 0
      });

      this.axios({
        method: "GET",
        params: {
          key: this.appkey,
          name: this.value
        }
      })
        .then(result => {
          this.$toast.clear();
          if (result.data.code == 200) {
             result.data.newslist.forEach(v => {
              v.Collection = false;
              v.styleTitle='mySearchData';
              v.listType=null;
              v.saveAddNum=null;
            });
             this.showSearchData = result.data.newslist;
             this.changeShowOffData("block");
               this.showOff="block";
           
            this.addMySearchData(result.data.newslist[0]);
            this.addSearchDataShow(result.data.newslist)
           
               
          } else {
            this.$toast("请输入正确的宠物名称，比如：哈士奇");
            return;
          }
        })
        .catch(err => {
          this.$toast.clear();
        });
    },
    Off(){
      this.changeShowOffData("none");
      this.showOff="none";
      
    }
  }
};
</script>